<template>
  <div class="chat-container">
    <el-container
      class="chat-box"
      v-loading="loading"
      element-loading-text="正在登录计划聊天室">

      <el-header
        height="30px"
        class="header-text send-online"
        v-if="sendOnlineStatus"
      >
        向在线用户发送消息

        <div class="header-close" @click="returnToMain">
          <icon
            class="close-icon"
            name="times"
            scale="1"
          />
        </div>
      </el-header>

      <el-header
        height="30px"
        class="header-text private-message"
        v-else-if="hasTarget"
      >
        私人信息
        {{ conversationTarget.username && ' - ' }}
        {{ conversationTarget.nickname || conversationTarget.username || '' }}

        <div class="header-close" @click="returnToMain">
          <icon
            class="close-icon"
            name="times"
            scale="1"
          />
        </div>
      </el-header>

      <el-main class="content" id="chatBox">
        <ul>
          <li v-for="(item, index) in roomMessages"
            :key="index"
            :class="
              ['clearfix',
                'item',
                item.extra.sender && (user.username === item.extra.sender.username) ?
                'item-right' : 'item-left', item.extra.type < 0 ?
                'sys-msg' : ''
              ]">
            <div class="lay-block clearfix" v-if="item.extra.type >= 0 && item.extra.type !== 6">
              <div class="avatar" @click="handleAvatarClick(item)">
                <icon name="cog" class="font-cog" v-if="item.extra.type == 4" scale="3"></icon>
                <img :src="item.extra.sender && item.extra.sender.avatar ? item.extra.sender.avatar : require('../assets/avatar.png')" v-else>
              </div>
              <div class="lay-content">
                <div class="msg-header">
                  <h4>{{
                      item.extra.type === 4 ?
                      '计划消息' : item.extra.sender && item.extra.sender.username === user.username && user.nickname ?
                      user.nickname : item.extra.sender && (item.extra.sender.nickname || item.extra.sender.username)
                    }}
                  </h4>
                  <img class="msg-label" v-if="item.extra.sender && item.extra.sender.label" :src="item.extra.sender.label.icon">
                  <span class="msg-time">{{item.extra.created_at | moment('HH:mm:ss')}}</span>
                </div>
                <div v-if="item.extra.type === 5">
                  <div v-if="(myRoles && myRoles.includes('visitor'))" class="envelope-message expired">
                    <img class="img m-r" src="../assets/envelope_message.png" alt="envelope"/>
                    <div class="send-texts">
                      <p class="slogan">{{item.content ? item.content : '恭喜发财 大吉大利'}}</p>
                      <p class="action">会员才可以抢红包！</p>
                    </div>
                  </div>
                  <div :class="['envelope-message','pointer',
                    {'null': (item.extra.envelope_status.users.length && item.extra.envelope_status.total === item.extra.envelope_status.users.length) && !item.extra.envelope_status.users.map(item => item.receiver_id).includes(user.id)}]"
                    v-else-if="item.extra.envelope_status && !item.extra.envelope_status.expired"
                    @click="takeEnvelope(item, index)">
                    <img class="img m-r" src="../assets/envelope_message.png" alt="envelope"/>
                    <div class="send-texts">
                      <p class="slogan">{{item.content ? item.content : '恭喜发财 大吉大利'}}</p>
                      <p class="action">
                        {{
                          item.extra.envelope_status.users.map(item => item.id).includes(user.id) ?
                          '已领取' : (item.extra.envelope_status.total === item.extra.envelope_status.users.length)  ?
                          '已领完' : '待领取'
                        }}
                      </p>
                    </div>
                  </div>
                  <div class="envelope-message expired"
                    v-else-if="item.extra.envelope_status.expired">
                    <img class="img m-r"
                      src="../assets/envelope_message.png"
                      alt="envelope"/>
                    <div class="send-texts">
                      <p class="slogan">{{item.content ? item.content : '恭喜发财 大吉大利'}}</p>
                      <p>已过期</p>
                    </div>
                  </div>
                </div>
                <div class="text-center sticker-msg" v-else-if="item.extra.type === 7">
                  <img-async class="img"
                    :src="item.content"
                    @imgStart="imgLoadCount++"
                    @imgLoad="imgLoadCount--"/>
                </div>
                <div :class="['bubble', 'bubble' + item.extra.type]" v-else>
                  <div>
                    <span class="item-content" v-if="item.extra.type === 0 || item.extra.type === 4 || item.extra.type === 8">{{item.content}}</span>
                    <p v-else-if="item.extra.type === 1">
                      <video v-if="videoType(item.content)"
                        :src="item.content"
                        :poster="item.extra.thumbnail"
                        controls
                        preload
                        width="100%">
                        您的浏览器不支持 video 播放
                      </video>
                      <img-async @click.native="showImageMsg = true; showImageMsgUrl = item.content"
                        v-else="!videoType(item.content)"
                        :src="item.content"
                        @imgStart="imgLoadCount++"
                        @imgLoad="imgLoadCount--"/>
                    </p>
                  </div>
                </div>
              </div>
            </div>
           <!--  <div class="text-center" v-else-if="item.extra.type === 6">
              <p class="get-envelope">{{`${item.extra.get_envelope_user.id === user.id ? '你' : item.extra.get_envelope_user.nickname}抢到了你的红包`}}</p>
            </div> -->
          </li>
          <li v-if="personalSetting.blocked" class="block-user-info">您已被管理员拉黑，请联系客服。<li>
          <li ref="msgEnd" id="msgEnd" class="msgEnd"></li>
        </ul>
      </el-main>
      <el-footer class="footer m-t"
        height="100">
        <div class="control-bar">
          <el-popover
            :disabled="myRoles.includes('visitor') || personalSetting.blocked"
            v-model="showStickerPopover"
            :popper-class="'emoji-popover'"
            ref="popover4"
            placement="top-start"
            :width="stickerTab === 'stickers' ? 460 : 300"
            trigger="click">
            <el-tabs type="border-card" class="stickers-tab" v-model="stickerTab">
              <el-tab-pane label="表情符号" name="emojis">
                <div class="emoji-container">
                  <Emojis :emojis="emojis.people.slice(0, 42)" @emojiClick="handleEmojiClick"/>
                </div>
              </el-tab-pane>
              <el-tab-pane v-if="stickerGroups.length" label="表情包" name="stickers">
                <div class="stickers-container">
                  <Stickers @sendSticker="sendSticker" v-if="stickerTab === 'stickers'"/>
                </div>
              </el-tab-pane>
            </el-tabs>
          </el-popover>
          <a v-popover:popover4
            v-if="emojiSuccess"
            @click="handleEmojiIconClick"
            href="javascript:void(0)"
            title="发送表情"
            :class="['btn-control','btn-smile', 'pointer', {'not-allowed': personalSetting.blocked}]">
            <icon scale="1.3" name="smile-o"></icon>
          </a>
          <a href="javascript:void(0)" :class="['btn-control', 'btn-smile', 'pointer', {'not-allowed': personalSetting.blocked}]">
            <label for="imgUploadInput" @click="handleImgIconClick($event)">
              <span title="上传图片">
                <i :class="['el-icon-picture', {'not-allowed': personalSetting.blocked}]"></i>
                <input :disabled="!chatable"
                  @change="uploadMedia($refs.fileImgSend)"
                  type="file"
                  ref="fileImgSend"
                  class="img-upload-input"
                  id="imgUploadInput"
                  accept=".jpg, .png, .gif, .jpeg, image/jpeg, image/png, image/gif">
              </span>
            </label>
          </a>
          <a
            href="javascript:void(0)"
            :class="[
              'btn-control',
              'btn-smile',
              'pointer',
              { 'not-allowed': personalSetting.blocked }
            ]"
            v-show="!isPrivateMessage"
          >
            <label for="videoUploadInput" @click="handleImgIconClick($event)">
              <span title="上传视频">
                <icon name="camera" class="font-camera" scale="1.3"></icon>
                <input :disabled="!chatable"
                  @change="uploadMedia($refs.fileVideoSend)"
                  type="file"
                  ref="fileVideoSend"
                  class="img-upload-input"
                  id="videoUploadInput"
                  accept="video/*">
              </span>
            </label>
          </a>
          <div
            v-if="
              globalPreference.envelope_settings &&
              globalPreference.envelope_settings.enabled === '1' &&
              !isPrivateMessage
            "
            :class="[
              'envelope-icon',
              'pointer',
              { 'not-allowed': personalSetting.blocked || personalSetting.banned }
            ]"
            @click="handleEnvelopeIconClick"
          >
            <img class="img" src="../assets/envelope_icon.png" alt="envelope-icon">
          </div>
          <span v-if="myRoles.includes('manager')" class="btn-control right" @click="openManageDialog()" >
            <icon name="cog" class="font-cog" scale="1.4"></icon>
          </span>
          <!-- <div class="chat-buttons"
            v-if="ws &&
              (!myRoles.includes('customer service') &&
              !myRoles.includes('manager') &&
              !myRoles.includes('visitor'))">
            <el-button :type="room.room.read ? '' : 'warning'"
              class="chat-button"
              :plain="room.room.read"
              :key="index"
              size="mini"
              @click.native="openChatDialog(room)"
              v-for="(room, index) in rooms">
              {{`客服 ${index + 1}`}}
            </el-button>
          </div> -->
        </div>
        <div class="typing">
          <div :class="['txtinput', 'el-textarea', !chatable ? 'is-disabled' : '']">
            <textarea
              placeholder="请文明发言"
              type="textarea" rows="2"
              autocomplete="off"
              validateevent="true"
              class="el-textarea-inner"
              v-model="msgContent"
              :disabled="!chatable"
            >
            </textarea>
          </div>
          <div class="sendbtn fr">
            <a href="javascript:void(0)" :class="['u-btn', { 'not-allowed': !chatable }]" @click="sendMsg()">发送</a>
          </div>
        </div>
      </el-footer>
    </el-container>
    <!-- chatting image lightbox -->
    <el-dialog :visible.sync="showImageMsg"
      width="640px"
      custom-class="text-center"
      append-to-body>
      <img :src="showImageMsgUrl" class="popup-uploadedimage">
    </el-dialog>
    <!-- for Restraint member in chatHall -->
    <el-dialog
      title="管理"
      :visible.sync="restraint.dialogVisible"
      :width="restraint.content === 'all' ? '700px' : '30%'"
      class="restraint-dialog"
      top="5vh"
      :append-to-body="true"
      :modal-append-to-body="true"
      center>
      <Restraint v-if="restraint.dialogVisible"
        :restraint="restraint"
        :blockedUsers="blockedUsers"
        :bannedUsers="formattedBannerUsers"
        :RECEIVER="restraint.user.default_room || 1"
        @updateUsers="updateUsers"
        @handleUserRelease="handleUserRelease"
        @handleUserRestraint="handleUserRestraint"
      />
    </el-dialog>
    <!-- private chat dialog -->
     <el-dialog
       class="privatechat-dialog"
       v-if="myRoles.length === 1 && myRoles[0] === 'member'"
       :visible.sync="chat.dialogVisible"
       :width="'550px'"
       @close="closeChatDialog()"
       top="10vh"
       center>
      <PrivateChat :emojis="emojis"
        :joinChatRoom="beforeJoinChatRoom"
        :roomMsgs="roomMsgs"
        :chat="chat"
        v-if="chat.current.roomId && chat.current.type === 2"/>
    </el-dialog>
    <!-- red envelope dialog -->
     <el-dialog
       class="red-envelope-dialog"
       :visible.sync="envelope.visible"
       :width="'400px'"
       top="10vh"
       center>
      <Envelope :status="envelope.status"
        v-if="envelope.visible"
        :content="envelope.content"
        :envelope="envelope.envelope"
        :joinChatRoom="beforeJoinChatRoom"
        @closeDialog="closeEnvelope"
        @handleSend="handleEnvelopeSend"/>
    </el-dialog>
  </div>
</template>

<script>
import { init } from '../utils/initRy'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/cog'
import 'vue-awesome/icons/smile-o'
import 'vue-awesome/icons/camera'
import 'vue-awesome/icons/times'
import {
  fetchChatEmoji,
  fetchMember,
  fetchRyToken,
  getChatUser,
  sendMessageOnline,
  sendMediaToChat,
  takeEnvelope
} from '../api'
import { mapGetters, mapState } from 'vuex'
import PrivateChat from '../components/PrivateChat'
import Envelope from '../components/Envelope'
import Restraint from './Restraint'
import ImgAsync from './ImgAsync'
import Emojis from './Emojis'
import Stickers from './Stickers'
import api from '../api/urls'
import convMixins from '../mixins/conv_mixins'

export default {
  components: {
    Icon,
    Restraint,
    PrivateChat,
    Envelope,
    ImgAsync,
    Emojis,
    Stickers
  },
  mixins: [convMixins],
  data () {
    return {
      msgContent: '',
      showImageMsg: false,
      showImageMsgUrl: '',
      loading: false,
      emojis: {
        people: []
      },
      RECEIVER: '',
      blockedUsers: [],
      bannedUsers: [],
      restraint: {
        dialogVisible: false,
        user: '',
        content: '',
        nowTab: '0',
        tabs: [
          {
            display: '黑名单'
          },
          {
            display: '禁言'
          }
        ]
      },
      roomMessages: [],
      emojiSuccess: true,
      envelope: {
        content: '',
        status: '',
        visible: false,
        envelope: {}
      },
      showStickerPopover: false,
      stickerTab: 'emojis',
      imgLoadCount: 0,
      needScrollToBottom: false,
      ryToken: '',
      firstInit: true
    }
  },
  watch: {
    'conversationMessages' (val) {
      if (val.length) {
        this.leaveRoom(() => {
          this.$store.dispatch('setSendOnlineStatus', false)
          this.RECEIVER = `${this.conversationTarget.id}`
          this.joinChatRoom()
          this.roomMessages = val.map(item => item.content)
        })
      }
    },
    'sendOnlineStatus' (val) {
      if (val) {
        this.leaveRoom(() => {
          this.roomMessages = []
        })
      }
    },
    'imgLoadCount': function (count) {
      if (count === 0) {
        if (this.needScrollToBottom) {
          this.$nextTick(() => {
            this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
          })
        }
      }
    },
    'chat.current.roomId': {
      handler: function (val, oldVal) {
        if (
          (
            val === oldVal &&
            (this.hasTarget || this.sendOnlineStatus)
          ) ||
          val !== oldVal
        ) {
          this.$store.dispatch('setConversationTarget', 0)
          this.$store.dispatch('setConversationMessages', [])
          this.$store.dispatch('setSendOnlineStatus', false)

          this.loading = true
          this.RECEIVER = String(val)
          this.roomMessages = this.roomMessages || []
          !this.firstInit && (this.leaveRoom(() => {
            this.loading = false
            this.ws && (this.joinChatRoom())
          }))
        }
      },
      deep: true
    },
    'refreshRoom': function (count, oldCound) {
      if (count !== oldCound && count <= 4) {
        window.location.reload()
      }
    },
    'user.memberLogined': {
      handler: function (val, oldVal) {
        if (typeof val !== 'undefined' && val !== oldVal) {
          this.roomMessages = []
          this.leaveRoom(() => {
            this.ws && (this.ws.disconnect())
            this.beforeJoinChatRoom()
          })
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters([
      'myRoles'
    ]),
    ...mapState([
      'conversationMessages',
      'conversationTarget',
      'sendOnlineStatus',
      'globalPreference',
      'user',
      'chat',
      'roomMsgs',
      'envelopes',
      'stickerGroups',
      'ws',
      'connectStatus',
      'refreshRoom'
    ]),
    isLogin () {
      return this.$store.state.user.logined && this.$route.name !== 'Home'
    },
    formattedBannerUsers () {
      let result = []
      if (this.bannedUsers.length) {
        this.bannedUsers.forEach((item) => {
          let validBanned = this.$moment(item.banned_time).diff(this.$moment()) > 0
          if (validBanned) {
            result.push({
              username: item.username,
              banned_time: this.$moment(item.banned_time).fromNow(true)
            })
          }
        })

        return result
      }
    },
    chatable () {
      return !this.personalSetting.blocked && !this.personalSetting.banned
    },
    personalSetting () {
      return this.user[this.user.default_room_id] || {banned: false, blocked: false}
    },
    hasTarget () {
      return Object.keys(this.conversationTarget).length > 0
    },
    isPrivateMessage () {
      return this.sendOnlineStatus || this.hasTarget
    }
  },
  created () {
    localStorage.setItem('stickers', '{}')
  },
  mounted () {
    this.beforeJoinChatRoom()
  },
  methods: {
    leaveRoom (fn) {
      if (!this.ws) return
      this.roomMessages = []
      this.ws.quitChatRoom(this.RECEIVER, {
        onSuccess: function () {
          fn && fn()
        },
        onError: function () {
          console.log('退出聊天室失败')
        }
      })
    },
    handleUserRelease (releasedUser) {
      this.$emit('handleUserRelease', releasedUser)
    },
    handleUserRestraint (restraintedUser) {
      this.$emit('handleUserRestraint', restraintedUser)
    },
    handleImgIconClick (e) {
      if (this.personalSetting.blocked || this.personalSetting.banned) {
        e.preventDefault()
        return
      }
      if ((this.myRoles.length && this.myRoles.includes('visitor'))) {
        e.preventDefault()
        this.$store.dispatch('updateUnloginedDialog', {visible: true, status: 'Login'})
      }
    },
    handleEmojiIconClick () {
      if (this.myRoles.includes('visitor')) {
        this.$store.dispatch('updateUnloginedDialog', {visible: true, status: 'Login'})
      }
    },
    handleEmojiClick (emoji) {
      this.msgContent = this.msgContent + emoji.emoji + ' '
    },
    closeEnvelope () {
      this.envelope.visible = false
    },
    takeEnvelope (envelope, idx) {
      if (this.personalSetting.blocked || this.myRoles.includes('visitor')) {
        return
      }

      this.envelope.content = envelope.content
      this.envelope.status = 'taking'

      let payload = {
        envelope_id: envelope.extra.envelope_status.id,
        receiver_id: this.user.id
      }

      this.envelope.visible = true

      takeEnvelope(payload).then(res => {
        res.envelope_id = payload.envelope_id
        this.envelope.envelope = res

        if (res.amount) {
          this.$store.dispatch('fetchUser').then(() => { this.envelope.status = 'success' })
          // res.type = 6
          // this.sendMsg(res)
        } else {
          switch (res.status) {
            case 'expired' :
              this.envelope.status = 'expired'

              let index = this.roomMessages.findIndex((msg) => msg.extra.type === 5 && msg.extra.envelope_status && msg.extra.envelope_status.id === payload.envelope_id)
              this.roomMessages[index].extra.envelope_status.expired = true
              break
            case 'repeat' :
              this.envelope.status = 'repeat'
              break
            case 'fail' :
              this.envelope.status = 'fail'
              break
          }
        }

        this.roomMessages[idx].extra.envelope_status.users = res.users
      })
    },
    handleEnvelopeSend (envelope) {
      envelope.type = 5
      this.sendMsg(envelope)
      this.envelope.visible = false
      this.envelope.status = ''
      this.$store.dispatch('fetchUser')

      this.$nextTick(() => {
        this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
      })
    },
    handleEnvelopeIconClick () {
      if (this.personalSetting.blocked || this.personalSetting.banned) {
        return
      }
      if (this.myRoles.includes('visitor')) {
        this.$store.dispatch('updateUnloginedDialog', {visible: true, status: 'Login'})
      } else {
        this.envelope.status = 'sending'
        this.envelope.visible = true
        this.envelope.envelope = {}
      }
    },
    showingName (user) {
      return user.usernickname ? user.usernickname : user.username
    },
    getRoles (message) {
      return message.extra.sender.roles.map((role) => role.name)
    },
    beforeJoinChatRoom () {
      this.loading = true
      fetchRyToken().then((res) => {
        this.ryToken = res.chat_token
        this.$store.dispatch('setUser', {
          default_room_id: String(this.user.default_room_id) || (res.room_info.length ? res.room_info[0].id : '1')
        })
        this.$store.dispatch('switchRoom', {
          id: String(this.user.default_room_id) || (res.room_info.length ? res.room_info[0].id : '1'),
          room_info: res.room_info
        })
        this.connectRongClound(res.chat_token)
      }).catch(error => {
        console.log(error)
      })
    },
    connectRongClound (token) {
      let params = {
        appKey: api.appKey,
        token: token,
        store: this.$store
      }
      this.loading = true
      init(params, {
        getInstance: (ws) => {
          this.$store.dispatch('setWebsocket', ws)
          this.firstInit = false
          this.joinChatRoom()
        },
        receiveNewMessage: this.handleMsg
      })
      if (!this.emojis.people.length) {
        fetchChatEmoji().then((resData) => {
          this.emojis = resData
        }).catch(() => {
          this.emojiSuccess = false
        })
      }
    },
    joinChatRoom () {
      this.roomMessages = []
      this.ws && this.ws.joinChatRoom(this.RECEIVER, 30, {
        onSuccess: () => {
          this.loading = false
          this.$store.commit('UPDATE_CONNECT', {
            status: 0
          })
          this.$emit('chatRoomReady', true)
          this.scrollToLatest()
        },
        onError: (error) => {
          console.log(error)
          this.openMessageBox('加入聊天室失败，请刷新浏览器重试!', 'error')
        }
      })
    },
    getPersonalSetting () {
      if (this.ws) {
        this.ws.send(JSON.stringify({
          'command': 'user_profile'
        }))
      }
    },
    handleMsg (message) {
      this.loading = false
      let data = message.content
      let robotMsg = data.message

      if (robotMsg) {
        if (typeof robotMsg.content !== 'object') {
          return
        }

        let robotData = {}
        robotData.content = robotMsg.content.content
        robotMsg.content.type = Number(robotMsg.objectName)
        robotData.extra = JSON.stringify(Object.assign({}, robotMsg.content))
        data = robotData
      }

      let extra = typeof data.extra === 'string'
        ? JSON.parse(data.extra)
        : data.extra
      data.extra = extra

      switch (data.extra.type) {
        case 5:
          if (data.extra.sender && data.extra.sender.avatar !== null) {
            if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
              data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
            }
          }
          data.extra.envelope_status && (data.extra.envelope_status.users = [])
          this.roomMessages.push(data)
          this.$store.dispatch('collectEnvelope', extra)
          this.$nextTick(() => {
            this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
          })
          return
        case 6:
          if (data.extra.sender && data.extra.sender.avatar !== null) {
            if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
              data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
            }
          }
          this.roomMessages.push(data)
          this.$store.dispatch('collectEnvelope', extra)
          let currentEnvelopeIndex = this.roomMessages.findIndex((msg) => msg.envelope_status && msg.envelope_status.id === data.envelope_status.id)

          if (this.roomMessages[data.receivers]) {
            this.roomMessages[data.receivers][currentEnvelopeIndex].envelope_status = data.envelope_status
          }

          if (data.extra.sender.id === this.user.id) {
            this.$nextTick(() => {
              this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
            })
          }
          return
        default:
          if (data.command === 'live' || data.command === 'read_msg') {
            return
          }

          if (extra.type === 0) {
            data.content = unescape(data.content)
          }
          if (data.extra.sender && data.extra.sender.avatar !== null) {
            if (data.extra.sender.avatar.indexOf('//upload') !== -1) {
              data.extra.sender.avatar = data.extra.sender.avatar.replace('//upload', '/upload')
            }
          }

          if (extra.type === 7 && data.content && data.content !== null) {
            if (data.content.indexOf('//upload') !== -1) {
              data.content = data.content.replace('//upload', '/upload')
            }
          }

          !this.roomMessages && (this.roomMessages = [])

          this.roomMessages.push(data)

          let chatBox = document.getElementById('chatBox')
          let clientHeight = chatBox.clientHeight
          let scrollHeight = chatBox.scrollHeight

          this.$forceUpdate()
          let scrollTop = chatBox.scrollTop
          this.needScrollToBottom = true

          this.$nextTick(() => {
            if (scrollTop + clientHeight > (scrollHeight - 200) || (data.extra.sender && data.extra.sender.username === this.user.username)) {
              this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
            } else {
              this.needScrollToBottom = false
            }
          })
      }
      setTimeout(() => {
        this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
      }, 1000)
    },
    sendSticker (sticker) {
      this.showStickerPopover = false
      this.sendMsg(sticker)
    },
    uploadMedia (fileInp) {
      if (!this.chatable) {
        return
      }
      if (this.myRoles.includes('visitor')) {
        this.$store.dispatch('updateUnloginedDialog', {visible: true, status: 'Login'})
        return
      }
      let file = fileInp.files[0]
      let reg
      let size
      let type

      const isUploadImage = fileInp.id === 'imgUploadInput'

      if (isUploadImage) {
        type = '图片'
        reg = /\.(gif|jpg|jpeg|png)$/i
        size = this.globalPreference.max_media_size.image || 1
      } else {
        type = '视频'
        reg = /\.(flv|avi|wmv|mp4|mov)$/i
        size = this.globalPreference.max_media_size.video || 2
      }

      if (!reg.test(fileInp.value) || !this.chatable) {
        this.openMessageBox(type + '格式不正确或您目前尚不符合发言条件', 'error')
        return false
      }
      if (file.size > size * 1024 * 1024) {
        this.openMessageBox(type + '尺寸太大，请选择较小尺寸的' + type, 'error')
        return
      }
      let formData = new FormData()

      formData.append('receiver', this.RECEIVER)
      formData.append('media', file)

      const reader = new FileReader()
      reader.onloadend = (e) => {
        const modalMessage = isUploadImage
          ? `<img src="${e.target.result}" style="max-width: 100%; width: 100%;" />`
          : `
            <video
              controls
              preload
              width="100%"
            >
              <source
                src="${e.target.result}"
                type="video/mp4"
              >
              </source>
              您的浏览器不支持 video 播放
            </video>
          `

        this.$confirm(modalMessage, '确认媒体上传', {
          confirmButtonText: '上传',
          cancelButtonText: '取消',
          type: 'warning',
          center: true,
          dangerouslyUseHTMLString: true
        })
          .then(() => {
            sendMediaToChat(formData)
              .then((data) => {
                fileInp.value = ''
                data.type = 1
                this.sendMsg(data)
              })
          })
          .catch(() => {
            fileInp.value = ''
          })
      }

      reader.readAsDataURL(file)
    },
    createUserObject ({
      avatar,
      id,
      label,
      nickname,
      roles,
      username
    }) {
      return {
        'avatar': avatar,
        'id': id,
        'label': label || {},
        'nickname': nickname,
        'roles': roles,
        'username': username
      }
    },
    sendMsg (readyData) {
      if (!this.chatable) {
        return
      }
      if (this.myRoles.includes('visitor')) {
        this.$store.dispatch('updateUnloginedDialog', {visible: true, status: 'Login'})
        return
      }
      if (!this.msgContent.trim() && !readyData) { return false }

      if (!this.ws || this.connectStatus.status !== 0) {
        this.beforeJoinChatRoom()
      }

      const senderDetails = this.createUserObject({
        'avatar': this.user.avatar,
        'id': this.user.id,
        'label': this.user.label || {},
        'nickname': this.user.nickname,
        'roles': this.user.roles,
        'username': this.user.username
      })

      const msgData = {
        'sender': senderDetails,
        'type': readyData && readyData.type ? readyData.type : 0,
        'receivers': this.RECEIVER,
        'created_at': new Date().getTime(),
        'conversation_members': []
      }

      const isSendOnline = this.sendOnlineStatus
      const isPrivate = Object.keys(this.conversationTarget).length > 0
      const isManager = this.myRoles.includes('manager')

      // Add Conversation Members if message is Private
      if ((isPrivate || isSendOnline) && isManager) {
        msgData.conversation_members = [
          senderDetails,
          this.conversationTarget
        ]
      } else if (isPrivate || isSendOnline) {
        const lastMessage = this.roomMessages[this.roomMessages.length - 1]
        const { conversation_members: convMembers } = lastMessage.extra
        const filteredMembers = convMembers.filter(item => item.username)
        const hasUserId = filteredMembers.find(
          item => item.id === parseInt(this.user.id)
        )

        msgData.conversation_members = [...filteredMembers]

        if (!hasUserId) {
          msgData.conversation_members.push(senderDetails)
        }
      }

      let sendContent = readyData ? readyData.url : escape(this.msgContent)

      if (readyData) {
        switch (readyData.type) {
          case 1:
            msgData.thumbnail = readyData.thumbnail
            break
          case 5:
            sendContent = readyData.content ? readyData.content : '恭喜发财，大吉大利'
            msgData.envelope_status = {
              expired: false,
              id: readyData.id,
              total: readyData.divided_into,
              users: []
            }
            break
          case 6:
            msgData.get_envelope_user = {
              id: this.user.id,
              nickname: this.user.nickname || this.user.username
            }
            msgData.envelope_status = {
              id: readyData.id
            }
            break
        }
      }

      const content = {
        content: sendContent,
        extra: JSON.stringify(msgData)
      }

      const conversationtype = (isPrivate)
        ? RongIMLib.ConversationType.PRIVATE
        : RongIMLib.ConversationType.CHATROOM

      const msg = new RongIMLib.TextMessage(content)

      if (this.sendOnlineStatus) {
        return this.sendOnlineMessage(sendContent, msgData, readyData)
      }

      this.ws.sendMessage(conversationtype, this.RECEIVER, msg, {
        onSuccess: message => {
          !this.roomMessages && (this.roomMessages = [])
          this.roomMessages.push({
            content: !readyData ? unescape(sendContent) : sendContent,
            extra: msgData
          })
          this.msgContent = ''
          this.$nextTick(this.scrollToLatest)
        },
        onError: (errorCode, message) =>
          this.openMessageBox('发送消息失败', 'error')
      })
    },
    /**
     * THIS IS ONLY A TEMPORARY WORKAROUND
     * While waiting for RongCloud's response
     * on our filed issue
     */
    async sendOnlineMessage (content, extra, readyData) {
      try {
        const { users } = await sendMessageOnline()

        if (users.length) {
          const senderDetails = this.createUserObject({
            'avatar': this.user.avatar,
            'id': this.user.id,
            'label': this.user.label || {},
            'nickname': this.user.nickname,
            'roles': this.user.roles,
            'username': this.user.username
          })
          const members = await Promise.all(users.map(id => fetchMember(id)))

          let doneSending = false

          for (const member of members) {
            const receiverDetails = this.createUserObject({
              'avatar': member.avatar,
              'id': member.id,
              'label': member.label || {},
              'nickname': member.nickname,
              'roles': member.roles,
              'username': member.username
            })

            const convMembers = [receiverDetails, senderDetails]
            const contentObj = {
              content,
              extra: JSON.stringify({
                ...extra,
                conversation_members: convMembers
              })
            }

            const msg = new RongIMLib.TextMessage(contentObj)

            // Delay code execution
            await new Promise((resolve, reject) => {
              setTimeout(resolve, 300)
            })

            this.ws.sendMessage(
              RongIMLib.ConversationType.PRIVATE,
              member.id.toString(),
              msg,
              {
                onSuccess: message => {
                  if (!doneSending) {
                    !this.roomMessages && (this.roomMessages = [])
                    this.roomMessages.push({
                      content: !readyData ? unescape(content) : content,
                      extra
                    })

                    this.msgContent = ''

                    doneSending = true
                  }

                  this.$nextTick(this.scrollToLatest)
                },
                onError: (errorCode, message) => {
                  this.openMessageBox('发送消息失败', 'error')
                }
              }
            )
          }
        }
      } catch (e) {
        console.error(e)
      }
    },
    scrollToLatest () {
      this.$refs.msgEnd && this.$refs.msgEnd.scrollIntoView()
    },
    getUser () {
      getChatUser(1).then(response => {
        this.bannedUsers = response.banned_users
        this.blockedUsers = response.block_users
      })
    },
    updateUsers (data) {
      this.bannedUsers = data.banned_users
      this.blockedUsers = data.block_users
    },
    handleAvatarClick (message) {
      let userRole = message.extra.sender.roles.map((role) => role.name)
      if (this.myRoles.includes('manager')) {
        if (userRole.includes('manager')) {
          return
        }
        this.restraint.user = message.extra.sender
        this.restraint.content = 'single'
        this.restraint.dialogVisible = true
      }

      if (this.chat.current.roomId === this.user.default_room_id &&
        this.myRoles.includes('customer service') &&
        userRole.length === 1 &&
        userRole[0] === 'member') {
        this.$emit('handleAvatarClick', message.extra.sender)
      }
    },
    openManageDialog () {
      this.restraint.nowTab = '0'
      this.restraint.content = 'all'
      this.restraint.dialogVisible = true

      this.getUser()
    },
    openMessageBox (msg, type) {
      this.$message({
        showClose: true,
        message: msg,
        type: type
      })
    },
    videoType (str) {
      return /\.(flv|avi|wmv|mp4|mov)$/i.test(str)
    },
    returnToMain () {
      this.$store.dispatch('switchRoom', {
        id: this.user.default_room_id
      })
    }
  }
}
</script>
