import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

export function createStore () {
  return new Vuex.Store({
    state: {
      user: {
        logined: false,
        memberLogined: false,
        roles: [],
        default_room_id: ''
      },
      loading: false,
      globalPreference: {
        mobile_url: '',
        checkin_settings: {}
      },
      stickerGroups: [],
      chatList: [],
      chat: {
        dialogVisible: false,
        current: {
          roomId: ''
        },
        room_info: []
      },

      // Conversations
      conversations: [],
      conversationMessages: [],
      conversationTarget: {},
      sendOnlineStatus: false,
      unreadConversations: 0,

      restraintMembers: {},
      roomMsgs: {},
      rooms: [],
      envelopes: {},
      ws: null,
      connectStatus: {
        status: '',
        send: 0
      },
      unloginedDialog: {
        visible: false,
        status: 'Login'
      },
      refreshRoom: 0
    },
    actions,
    mutations,
    getters
  })
}



