import { mapGetters, mapState } from 'vuex'

const convMixins = {
  data () {
    return {
      loading: true
    }
  },
  computed: {
    ...mapGetters([
      'latestMessages'
    ]),
    ...mapState([
      'ws'
    ])
  },
  watch: {
    latestMessages () {
      this.getUnreadConversations()
    },
    ws () {
      this.getConversationList()
    }
  },
  methods: {
    clearUnreadCount (targetId, callbacks) {
      const onSuccess = (callbacks && callbacks.onSuccess)
        ? callbacks.onSuccess
        : () => console.log('cleared unread')

      const onError = (callbacks && callbacks.onError)
        ? callbacks.onError
        : err => console.error(err)

      this.ws.clearUnreadCount(
        RongIMLib.ConversationType.PRIVATE,
        targetId,
        { onSuccess, onError }
      )
    },
    getConversationList () {
      if (this.ws) {
        this.ws.getConversationList({
          onSuccess: conversationList => {
            this.loading = false
            this.$store.dispatch('setConversations', conversationList)
          },
          onError: error => {
            console.log(error)
          }
        }, null)
      }
    },
    getUnreadConversations () {
      if (this.ws) {
        this.ws.getConversationUnreadCount([RongIMLib.ConversationType.PRIVATE], {
          onSuccess: (count) => {
            console.log(`Count: ${count}`)
            this.$store.dispatch('setUnreadConversations', count)
          },
          onError: (error) => {
            console.error(error)
            this.$store.dispatch('setUnreadConversations', 0)
          }
        })
      }
    }
  }
}
export default convMixins



