<template>
  <div class="rooms-container">
    <ul class="list" v-if="chat.room_info.length">
      <li :class="['item', 'pointer',
        { active: chat.current.roomId === room.id }]"
        v-for="(room, index) in chat.room_info"
        @click="enterRoom(room)"
        :key="index">
        <div class="m-l">
          <!-- halls-->
          <i class="icon el-icon-info m-r"></i>
          <p class="title">{{index === 0 ? room.name : `${room.name} 聊天室大厅`}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  props: {
    defaultHall: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      previousRoom: {
        id: this.defaultHall,
        type: 1
      }
    }
  },
  methods: {
    enterRoom (room) {
      this.$store.dispatch('switchRoom', {
        id: room.id
      })
    },
    read (connection, roomId, lastMsg) {
      connection.send(JSON.stringify({
        command: 'read_msg',
        message: lastMsg.id,
        room: roomId,
        user: this.user.id
      }))

      this.$store.dispatch('updateRoomRead', { roomId: roomId, read: true })
    }
  },
  computed: {
    ...mapState([
      'chat',
      'rooms',
      'roomMsgs',
      'user',
      'ws',
      'connectStatus'
    ]),
    ...mapGetters([
      'myRoles'
    ])
  }
}
</script>

<style lang="scss" scoped>
.rooms-container {
  height: 100%;
  overflow-y: auto;
}

.list {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  .item {
    padding: 5px 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    &.unread {
      background: #FFB74D;
    }

    &.active {
      background:#1976D2;
      font-weight: 700;
    }

    &:hover {
      background: rgba(255, 255, 255, .2);
    }

    .icon {
      font-size: 18px;
      vertical-align: middle;
      color: #fff;
    }

    .img {
      width: 28px;
      height: 28px;
      vertical-align: middle;
    }

    .title {
      display: inline-block;
      width: 150px;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #fff;
      white-space: nowrap;
      font-size: 13px;
      vertical-align: middle;
    }
  }
}
</style>



