export default {
  user: (state, getters) => {
    return state.user
  },
  myRoles: (state, getters) => {
    return state.user.roles.map(role => role.name)
  },
  latestMessages: (state) => {
    const newConv = state.conversations
      .filter(conversation => conversation.targetId)
      .sort((a, b) => b.sentTime - a.sentTime)
      .map(conversation => {
        const { targetId } = conversation
        const { content } = conversation.latestMessage
        const contentExtra = typeof content.extra === 'string'
          ? JSON.parse(content.extra)
          : content.extra
        const { conversation_members: convMembers } = contentExtra

        const isContentURL = /(www|http)/ig.test(content.content)

        let target
        convMembers && convMembers.forEach(member => {
          const isIdEqual = parseInt(member.id) === parseInt(state.user.id)

          if (!isIdEqual && !target) {
            target = {...member}
          }
        })

        const data = {
          ...content,
          extra: contentExtra,
          content: isContentURL
          ? '发了一个媒体'
          : unescape(content.content),
          target: target || {},
          targetId,
          unreadCount: conversation.unreadMessageCount
        }

        return data
      })

    return newConv
  }
}



