import * as types from './mutation-types'
import Vue from 'vue'

export default {
  [types.SET_USER]: (state, data) => {
    state.user = Object.assign({}, state.user, data)
  },
  [types.RESET_USER]: (state, data = {}) => {
    state.user = {
      logined: false,
      roles: []
    }
    Vue.cookie.delete('access_token')
    Vue.cookie.delete('refresh_token')
  },
  [types.START_LOADING]: (state, loading) => {
    state.loading = true
  },
  [types.REFRESH_ROOM]: (state, loading) => {
    state.refreshRoom++
  },
  [types.END_LOADING]: (state) => {
    state.loading = false
  },
  [types.SET_GLOBAL]: (state, data) => {
    state.globalPreference = data
  },
  [types.SET_WEBSOCKET]: (state, data) => {
    state.ws = data
  },
  [types.UPDATE_CONNECT]: (state, data) => {
    state.connectStatus = Object.assign({}, state.connectStatus, data)
  },
  [types.UPDATE_CHATLIST]: (state, data) => {
    state.chatList = data || []
  },
  [types.UPDATE_ROOMREAD]: (state, data) => {
    let role = state.user.roles.map(role => role.name)
    let current = role.length === 1 && role[0] === 'member' ? state.rooms.find(room => room.room.id === data.roomId) : state.rooms.find(room => room.id === data.roomId)

    if (current) {
      if (current.hasOwnProperty('is_read_latest_message')) {
        current.is_read_latest_message = data.read
      } else {
        if (current.type !== 1) {
          current.room.read = data.read
        }
      }
    }
  },
  [types.UPDATE_UNLOGINEDDIALOG]: (state, data) => {
    state.unloginedDialog = data || {}
  },
  [types.SWITCH_ROOM]: (state, data) => {
    Vue.set(state, 'chat', {
      ...state.chat,
      ...data
    })

    state.chat.current.roomId = data.id ? data.id : (data.room_info.length && data.room_info[0].id)
  },
  [types.SET_ROOMS]: (state, data) => {
    if (data.roomId) {
      state.rooms[data.roomId] = data
    } else {
      state.rooms = data
    }
  },
  [types.SET_ROOMMSGS]: (state, data) => {
    if (data.roomId) {
      state.roomMsgs[data.roomId] = data
    } else {
      state.roomMsgs = data
    }
  },
  [types.SET_RESTRAINTMEMBERS]: (state, data) => {
    if (data.roomId) {
      state.restraintMembers[data.roomId] = data
    } else {
      state.restraintMembers = data
    }
  },
  [types.SET_STICKERGROUPS]: (state, data) => {
    state.stickerGroups = data
  },
  [types.COLLECT_ENVELOPE]: (state, data) => {
    Vue.set(state.envelopes, data.envelope_status.id, data)
  },
  [types.SET_CONVERSATIONS]: (state, data) => {
    state.conversations = data
      .filter(item => parseInt(item.targetId) !== 1)
      .map(item => {
        const { extra } = item.latestMessage.content

        if (!extra) {
          item.latestMessage.content.extra = {
            sender: {
              avatar: '',
              id: '',
              label: {},
              nickname: '',
              roles: [],
              username: ''
            },
            type: '',
            receivers: [],
            created_at: new Date().getTime()
          }
        }

        if (typeof extra === 'string') {
          item.latestMessage.content.extra = JSON.parse(extra)
        }

        return item
      })
  },
  [types.SET_CONVERSATION_MESSAGES]: (state, data) => {
    state.conversationMessages = data.map(item => {
      const { extra } = item.content
      if (!extra) {
        item.content.extra = {
          sender: {
            avatar: '',
            id: '',
            label: {},
            nickname: '',
            roles: [],
            username: ''
          },
          type: '',
          receivers: [],
          created_at: new Date().getTime()
        }
      } else if (typeof extra === 'string') {
        item.content.extra = JSON.parse(extra)
      }

      return item
    })
  },
  [types.SET_CONVERSATION_TARGET]: (state, data) => {
    state.conversationTarget = data
  },
  [types.SET_SEND_ONLINE_STATUS]: (state, data) => {
    state.sendOnlineStatus = data
  },
  [types.SET_UNREAD_CONVERSATIONS]: (state, data) => {
    state.unreadConversations = data
  }
}



