<template>
  <div>
    <div class="conversations-container">
      <!-- SEARCH INPUT -->
      <input
        type="search"
        placeholder="搜索"
        v-model="search"
      >
      <!-- LOADING ICON -->
      <icon
        v-show="conversations.length === 0 && loading"
        class="loading"
        name="spinner"
        scale="1.5"
        pulse
      />

      <!-- LATEST MESSAGES -->
      <ul>
        <li
          class="no-conversations"
          v-show="conversations.length === 0 && !loading"
        >
          没有对话
        </li>
        <li
          v-for="c in messages"
          :key="c.targetId"
          @click="getMessages(c.targetId)"
        >
          <div class="left">
            <span
              class="avatar"
              :style="{ backgroundImage: `url(${c.target.avatar})` }"
            />
          </div>

          <div class="right">
            <div class="name-notification">
              <span class="username">{{ c.target.nickname || c.target.username }}</span>
              <!-- <el-badge
                is-dot
                class="item notification-dot"
                v-show="c.unreadCount > 0"
              >
              </el-badge> -->
            </div>
            <p class="message">
              <span v-show="c.unreadCount > 0" style="font-weight:bold;">
                {{ c.extra.sender.nickname || c.extra.sender.username || '未知' }} :
                {{ c.content }}
              </span>
            </p>
          </div>

          <div class="loading-target">
            <icon
              v-if="loadingTarget === c.targetId"
              class="loading"
              name="spinner"
              scale="1"
              pulse
            />
          </div>
        </li>
      </ul>

      <el-button
        v-show="!myRoles.includes('member')"
        class="send-online"
        native-type="button"
        type="success"
        @click="openOnlineMessage"
        :disabled="!ws"
      >
        <span>
          <icon
            class="send-icon"
            name="paper-plane"
            scale="1"
          />
          &nbsp;
          向在线用户发送消息
        </span>
      </el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/paper-plane'
import 'vue-awesome/icons/spinner'
import convMixins from '../mixins/conv_mixins'

export default {
  name: 'Conversations',
  components: { Icon },
  mixins: [convMixins],
  data () {
    return {
      search: '',
      loadingTarget: 0,
      loading: true
    }
  },
  computed: {
    ...mapState([
      'conversations'
    ]),
    ...mapGetters([
      'latestMessages',
      'myRoles'
    ]),
    messages () {
      const {
        latestMessages,
        search
      } = this

      const searchVal = search.trim().toLowerCase()

      if (searchVal.length > 0) {
        return latestMessages.filter(item => {
          const { nickname, username } = item.target

          if (!nickname && !username) {
            return false
          }

          return (
            (nickname && nickname.toLowerCase().indexOf(searchVal) > -1) ||
            username.toLowerCase().indexOf(searchVal) > -1
          )
        })
      }

      return latestMessages
    }
  },
  mounted () {
    this.getConversationList()
  },
  methods: {
    getMessages (targetId) {
      const type = RongIMLib.ConversationType.PRIVATE
      const timestrap = 0
      const count = 20

      this.loadingTarget = targetId

      const onSuccess = messageList => {
        this.clearUnreadCount(targetId, {
          onSuccess: async () => {
            await this.$store.dispatch('setConversationTarget', targetId)
            this.$store.dispatch('setConversationMessages', messageList)
            this.$emit('closeConversations')
          }
        })
      }

      this.ws.getHistoryMessages(
        type,
        targetId,
        timestrap,
        count,
        { onSuccess }
      )
    },
    openOnlineMessage () {
      this.$store.dispatch('setSendOnlineStatus', true)
      this.$emit('closeConversations')
    }
  }
}
</script>

<style lang="scss" scoped>
ul {
  height: 200px;
  overflow-y: auto;
}

li {
  align-content: center;
  border-bottom: 1px solid #fff;
  display: flex;
  padding: 10px 5px;
  position: relative;

  &:hover {
    background-color: #ececec;
  }

  & .left,
  & .right {
    display: flex;
    flex-direction: column;
    position: relative;
  }

  & .left {
   justify-content: center;
  }

  & .right p {
    max-width: 220px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  & .message {
    font-size: 12px;
  }

  & .username {
    font-size: 12px;
    font-weight: bold;
  }
}

.avatar {
  background: #000;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px;
  border-radius: 50%;
  display: inline-block;
  height: 20px;
  margin-right: 10px;
  width: 20px;
}

.conversations-container {
  background: #fff;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  color: #606266;
  font-size: 14px;
  line-height: 1.4;
  padding: 12px;
  position: absolute;
  right: 0;
  text-align: justify;
  top: 64px;
  width: 300px;
  z-index: 2001;

  & input {
    border: none;
    border-bottom: 1px solid #d9d9d9;
    margin: 0;
    padding: 5px 0;
    outline: none;
    width: 100%;
  }
}

.loading {
  margin: 10px 0;
  width: 100%;
}

.loading-target {
  position: absolute;
  right: 10px;
}

.no-conversations {
  justify-content: center;
}

.notification-dot {
  top: -5px;
  right: 0;
}

.notification {
  right: 34px;
}

.send-online {
  margin-top: 10px;
  width: 100%;

  & span {
    align-items: center;
    display: flex;
    justify-content: center;
  }
}
</style>



